
import { ref, SetupContext } from "vue";
export default {
  name: "BaseStatus",
  emits: ["onclick"],
  props: {
    title: {
      type: String,
      required: true,
    },
    content: {
      type: String,
      required: true,
    },
    highlight: {
      type: Boolean,
      required: false,
    },
  },
  setup(_: unknown, context: SetupContext) {
    return { isClickable: ref(context.attrs.onclick) };
  },
};
